* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Noto Sans KR', -apple-system, BlinkMacSystemFont, 'Segoe UI', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.App {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 20px;
  transition: background 0.3s ease;
}

/* Îã§ÌÅ¨Î™®Îìú/ÎùºÏù¥Ìä∏Î™®Îìú - ÌÇ§Ìã∞ Ïä§ÌÉÄÏùº */
.App.dark-mode {
  background: linear-gradient(135deg, #ff6b9d 0%, #c44569 50%, #6c5ce7 100%);
}

.App.light-mode {
  background: linear-gradient(135deg, #ffeef8 0%, #ffe5f1 50%, #ffd6e8 100%);
}

.App.timer-active.dark-mode {
  background: linear-gradient(135deg, #ff6b9d 0%, #c44569 100%);
}

.App.timer-active.light-mode {
  background: linear-gradient(135deg, #fff0f6 0%, #ffe0ec 100%);
}

.container {
  max-width: 800px;
  width: 100%;
}

/* Ìó§Îçî - Í∑ÄÏó¨Ïö¥ Ïä§ÌÉÄÏùº */
.header {
  text-align: center;
  color: white;
  margin-bottom: 30px;
  position: relative;
}

.light-mode .header {
  color: #ff6b9d;
}

.header h1 {
  font-size: 3em;
  margin-bottom: 10px;
  text-shadow: 3px 3px 6px rgba(255, 107, 157, 0.4);
  font-weight: 900;
  letter-spacing: -1px;
}

.light-mode .header h1 {
  text-shadow: 2px 2px 4px rgba(255, 107, 157, 0.2);
}

.subtitle {
  font-size: 1.2em;
  opacity: 0.95;
  font-weight: 600;
}

.header-buttons {
  display: flex;
  gap: 12px;
  justify-content: center;
  margin-top: 20px;
}

.icon-btn {
  background: linear-gradient(135deg, #ff6b9d 0%, #ff8fab 100%);
  border: none;
  border-radius: 50%;
  width: 55px;
  height: 55px;
  font-size: 1.6em;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 5px 15px rgba(255, 107, 157, 0.3);
}

.icon-btn:hover {
  transform: scale(1.15) rotate(5deg);
  box-shadow: 0 8px 20px rgba(255, 107, 157, 0.5);
}

.light-mode .icon-btn {
  background: linear-gradient(135deg, #ff6b9d 0%, #ffa0c0 100%);
  box-shadow: 0 5px 15px rgba(255, 107, 157, 0.2);
}

/* D-Day Î∞∞ÎÑà - Î¶¨Î≥∏ Ïä§ÌÉÄÏùº */
.dday-banner {
  background: linear-gradient(135deg, #ff6b9d 0%, #ff8fab 100%);
  border-radius: 20px;
  padding: 20px;
  text-align: center;
  color: white;
  font-size: 1.4em;
  margin-bottom: 15px;
  box-shadow: 0 8px 25px rgba(255, 107, 157, 0.3);
  font-weight: 700;
  position: relative;
  overflow: hidden;
}

.dday-banner::before {
  content: 'üéÄ';
  position: absolute;
  top: -10px;
  right: 20px;
  font-size: 2em;
  opacity: 0.3;
}

.light-mode .dday-banner {
  background: linear-gradient(135deg, #ff8fab 0%, #ffacc0 100%);
  color: white;
}

/* Ïä§Ìä∏Î¶≠ Î∞∞ÎÑà - ÌïòÌä∏ Ïä§ÌÉÄÏùº */
.streak-banner {
  background: linear-gradient(135deg, #ff6b9d 0%, #ff4d8a 100%);
  border-radius: 20px;
  padding: 20px;
  text-align: center;
  color: white;
  font-size: 1.4em;
  margin-bottom: 15px;
  box-shadow: 0 8px 25px rgba(255, 77, 138, 0.3);
  font-weight: 700;
  animation: heartBeat 1.5s infinite;
}

@keyframes heartBeat {
  0%, 100% { transform: scale(1); }
  25% { transform: scale(1.03); }
  50% { transform: scale(1); }
}

/* Îπ†Î•∏ Ï†ïÎ≥¥ - Îë•Í∑º Ïπ¥Îìú */
.quick-info {
  display: flex;
  gap: 12px;
  margin-bottom: 25px;
}

.quick-info-item {
  flex: 1;
  background: rgba(255, 255, 255, 0.25);
  backdrop-filter: blur(15px);
  border-radius: 18px;
  padding: 15px;
  text-align: center;
  color: white;
  font-size: 0.95em;
  box-shadow: 0 5px 15px rgba(255, 107, 157, 0.2);
  border: 2px solid rgba(255, 255, 255, 0.3);
}

.light-mode .quick-info-item {
  background: rgba(255, 255, 255, 0.9);
  color: #ff6b9d;
  border: 2px solid rgba(255, 107, 157, 0.2);
}

.quick-info-item span {
  display: block;
  opacity: 0.9;
  font-size: 0.9em;
  font-weight: 600;
}

.quick-info-item strong {
  display: block;
  font-size: 1.3em;
  margin-top: 5px;
  font-weight: 800;
}

/* Î™®ÏùòÍ≥†ÏÇ¨ Î≤ÑÌäº - Í∑ÄÏó¨Ïö¥ Î¶¨Î≥∏ */
.mock-exam-btn {
  width: 100%;
  padding: 22px;
  background: linear-gradient(135deg, #ff6b9d 0%, #ff4d8a 100%);
  color: white;
  border: none;
  border-radius: 20px;
  font-size: 1.4em;
  font-weight: 800;
  cursor: pointer;
  margin-bottom: 30px;
  transition: all 0.3s ease;
  box-shadow: 0 8px 20px rgba(255, 77, 138, 0.4);
  position: relative;
}

.mock-exam-btn::after {
  content: '‚ú®';
  position: absolute;
  right: 25px;
  top: 50%;
  transform: translateY(-50%);
  font-size: 1.2em;
}

.mock-exam-btn:hover {
  transform: translateY(-5px) scale(1.02);
  box-shadow: 0 12px 30px rgba(255, 77, 138, 0.5);
}

/* Í≥ºÎ™© Í∑∏Î¶¨Îìú - ÌíçÏÑ† Ïä§ÌÉÄÏùº */
.subject-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
  gap: 20px;
  margin-bottom: 30px;
}

.subject-card {
  background: white;
  border-radius: 25px;
  padding: 35px 20px;
  text-align: center;
  cursor: pointer;
  transition: all 0.3s ease;
  border: 4px solid;
  box-shadow: 0 10px 30px rgba(255, 107, 157, 0.2);
  position: relative;
  overflow: hidden;
}

.subject-card::before {
  content: '‚ú®';
  position: absolute;
  top: 10px;
  right: 10px;
  font-size: 1.5em;
  opacity: 0;
  transition: all 0.3s ease;
}

.subject-card:hover {
  transform: translateY(-15px) rotate(2deg);
  box-shadow: 0 15px 40px rgba(255, 107, 157, 0.4);
}

.subject-card:hover::before {
  opacity: 1;
  transform: rotate(15deg);
}

.subject-card:active {
  transform: translateY(-8px) scale(0.98);
}

.subject-emoji {
  font-size: 3.5em;
  margin-bottom: 12px;
  filter: drop-shadow(2px 2px 4px rgba(0, 0, 0, 0.1));
}

.subject-name {
  font-size: 1.4em;
  font-weight: 800;
  color: #333;
  margin-bottom: 8px;
}

.subject-time {
  font-size: 1.15em;
  color: #ff6b9d;
  font-weight: 700;
}

/* Ï†ïÎ≥¥ Î∞ïÏä§ - Íµ¨Î¶Ñ Ïä§ÌÉÄÏùº */
.info-box {
  background: rgba(255, 255, 255, 0.25);
  backdrop-filter: blur(15px);
  border-radius: 20px;
  padding: 25px;
  color: white;
  text-align: center;
  border: 3px solid rgba(255, 255, 255, 0.3);
  box-shadow: 0 8px 20px rgba(255, 107, 157, 0.2);
}

.light-mode .info-box {
  background: rgba(255, 255, 255, 0.9);
  color: #ff6b9d;
  border: 3px solid rgba(255, 107, 157, 0.2);
}

.info-box p {
  margin: 10px 0;
  font-size: 1.05em;
  font-weight: 600;
}

/* Î™®Îã¨ - ÌÇ§Ìã∞ Ïä§ÌÉÄÏùº */
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(255, 107, 157, 0.75);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
  padding: 20px;
  backdrop-filter: blur(10px);
}

.modal-content {
  background: white;
  border-radius: 30px;
  padding: 45px;
  max-width: 500px;
  width: 100%;
  box-shadow: 0 20px 60px rgba(255, 107, 157, 0.4);
  animation: modalBounce 0.4s ease;
  border: 4px solid #ff6b9d;
}

@keyframes modalBounce {
  0% { transform: scale(0.5); opacity: 0; }
  50% { transform: scale(1.05); }
  100% { transform: scale(1); opacity: 1; }
}

.modal-content h2 {
  text-align: center;
  margin-bottom: 20px;
  color: #ff6b9d;
  font-size: 2.2em;
  font-weight: 900;
}

.modal-content p {
  text-align: center;
  color: #666;
  margin-bottom: 30px;
  font-size: 1.15em;
  font-weight: 600;
}

.modal-desc {
  font-size: 1em !important;
  color: #999 !important;
  margin-bottom: 20px !important;
}

/* Ìú¥Ïãù ÏòµÏÖò - ÌïòÌä∏ Î≤ÑÌäº */
.break-options {
  display: flex;
  gap: 12px;
  margin-bottom: 20px;
}

.break-options button {
  flex: 1;
  padding: 18px;
  background: linear-gradient(135deg, #ff6b9d 0%, #ff8fab 100%);
  color: white;
  border: none;
  border-radius: 15px;
  font-size: 1.05em;
  font-weight: 800;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 5px 15px rgba(255, 107, 157, 0.3);
}

.break-options button:hover {
  transform: translateY(-3px) scale(1.05);
  box-shadow: 0 8px 20px rgba(255, 107, 157, 0.5);
}

.skip-btn {
  width: 100%;
  padding: 15px;
  background: #ffc0cb;
  color: #ff6b9d;
  border: none;
  border-radius: 15px;
  font-size: 1.05em;
  font-weight: 700;
  cursor: pointer;
  transition: all 0.3s ease;
}

.skip-btn:hover {
  background: #ffb0be;
  transform: translateY(-2px);
}

/* Î™®ÏùòÍ≥†ÏÇ¨ Î™®Îã¨ */
.mock-exam-modal {
  max-width: 600px;
  border: 4px solid #ff6b9d;
}

.mock-exam-schedule {
  background: #fff5f8;
  border-radius: 20px;
  padding: 25px;
  margin-bottom: 25px;
  border: 3px solid #ffc0cb;
}

.schedule-item {
  display: flex;
  align-items: center;
  padding: 15px 0;
  border-bottom: 2px dashed #ffc0cb;
}

.schedule-item:last-child {
  border-bottom: none;
}

.schedule-subject {
  flex: 1;
  font-size: 1.2em;
  font-weight: 800;
  color: #ff6b9d;
}

.schedule-time {
  color: #ff6b9d;
  font-weight: 800;
  font-size: 1.1em;
  margin-right: 12px;
}

.schedule-break {
  color: #ff8fab;
  font-size: 0.95em;
  font-weight: 700;
}

.total-time {
  text-align: center;
  color: #ff6b9d;
  font-weight: 800;
  margin-bottom: 30px;
  font-size: 1.1em;
}

.modal-buttons {
  display: flex;
  gap: 12px;
}

.start-mock-btn {
  flex: 1;
  padding: 18px;
  background: linear-gradient(135deg, #ff6b9d 0%, #ff4d8a 100%);
  color: white;
  border: none;
  border-radius: 15px;
  font-size: 1.2em;
  font-weight: 800;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 5px 15px rgba(255, 77, 138, 0.3);
}

.start-mock-btn:hover {
  transform: translateY(-3px);
  box-shadow: 0 8px 20px rgba(255, 77, 138, 0.5);
}

.cancel-btn {
  flex: 1;
  padding: 18px;
  background: #ffc0cb;
  color: #ff6b9d;
  border: none;
  border-radius: 15px;
  font-size: 1.2em;
  font-weight: 800;
  cursor: pointer;
  transition: all 0.3s ease;
}

.cancel-btn:hover {
  background: #ffb0be;
  transform: translateY(-2px);
}

/* Ï§ÄÎπÑ ÌôîÎ©¥ - Î∞òÏßùÏù¥Îäî Î≥Ñ */
.ready-container {
  max-width: 600px;
  width: 100%;
  text-align: center;
  color: white;
}

.light-mode .ready-container {
  color: #ff6b9d;
}

.ready-subject {
  font-size: 3.5em;
  font-weight: 900;
  margin-bottom: 35px;
  text-shadow: 3px 3px 6px rgba(255, 107, 157, 0.4);
  animation: sparkle 1.5s infinite;
}

@keyframes sparkle {
  0%, 100% { filter: brightness(1); }
  50% { filter: brightness(1.2); }
}

.ready-tip {
  background: rgba(255, 255, 255, 0.25);
  backdrop-filter: blur(15px);
  border-radius: 25px;
  padding: 35px;
  font-size: 1.4em;
  line-height: 1.7;
  margin-bottom: 45px;
  border: 3px solid rgba(255, 255, 255, 0.3);
  box-shadow: 0 8px 20px rgba(255, 107, 157, 0.2);
  font-weight: 600;
}

.light-mode .ready-tip {
  background: rgba(255, 255, 255, 0.95);
  color: #ff6b9d;
  border: 3px solid rgba(255, 107, 157, 0.2);
}

.ready-countdown {
  font-size: 9em;
  font-weight: 900;
  margin-bottom: 25px;
  animation: countdownPop 1s infinite;
  text-shadow: 5px 5px 10px rgba(255, 107, 157, 0.5);
}

@keyframes countdownPop {
  0%, 100% { transform: scale(1); }
  50% { transform: scale(1.15); }
}

.ready-message {
  font-size: 1.6em;
  opacity: 0.95;
  font-weight: 700;
}

/* ÌÉÄÏù¥Î®∏ ÌôîÎ©¥ - ÌíçÏÑ† Ïä§ÌÉÄÏùº */
.timer-container {
  max-width: 600px;
  width: 100%;
  position: relative;
}

.mock-exam-progress {
  text-align: center;
  color: white;
  font-size: 1.3em;
  margin-bottom: 25px;
  padding: 15px;
  background: rgba(255, 255, 255, 0.25);
  backdrop-filter: blur(15px);
  border-radius: 18px;
  font-weight: 700;
  border: 2px solid rgba(255, 255, 255, 0.3);
}

.light-mode .mock-exam-progress {
  background: rgba(255, 255, 255, 0.9);
  color: #ff6b9d;
  border: 2px solid rgba(255, 107, 157, 0.2);
}

.timer-header {
  text-align: center;
  margin-bottom: 45px;
}

.timer-header h2 {
  color: white;
  font-size: 3em;
  text-shadow: 3px 3px 6px rgba(255, 107, 157, 0.4);
  font-weight: 900;
}

.light-mode .timer-header h2 {
  color: #ff6b9d;
  text-shadow: 2px 2px 4px rgba(255, 107, 157, 0.2);
}

.timer-display {
  background: white;
  border-radius: 35px;
  padding: 70px 45px;
  text-align: center;
  margin-bottom: 35px;
  box-shadow: 0 20px 60px rgba(255, 107, 157, 0.3);
  transition: all 0.3s ease;
  border: 5px solid #ff6b9d;
}

.timer-display.break-mode {
  background: linear-gradient(135deg, #ff6b9d 0%, #ff8fab 100%);
  border-color: #ff4d8a;
}

.timer-display.warning {
  background: #fff9e6;
  border-color: #ffd93d;
  animation: pulse-warning 2s infinite;
}

.timer-display.critical {
  background: #ffe6e6;
  border-color: #ff6b9d;
  animation: pulse-critical 1s infinite;
}

@keyframes pulse-warning {
  0%, 100% { transform: scale(1); box-shadow: 0 20px 60px rgba(255, 217, 61, 0.5); }
  50% { transform: scale(1.03); box-shadow: 0 25px 70px rgba(255, 217, 61, 0.7); }
}

@keyframes pulse-critical {
  0%, 100% { transform: scale(1); box-shadow: 0 20px 60px rgba(255, 107, 157, 0.6); }
  50% { transform: scale(1.05); box-shadow: 0 25px 70px rgba(255, 107, 157, 0.8); }
}

.time {
  font-size: 7em;
  font-weight: 900;
  color: #ff6b9d;
  font-variant-numeric: tabular-nums;
  letter-spacing: 0.05em;
  text-shadow: 3px 3px 6px rgba(255, 107, 157, 0.2);
}

.timer-display.break-mode .time {
  color: white;
}

.alert-text {
  margin-top: 25px;
  font-size: 1.4em;
  font-weight: 800;
  color: #ffa500;
}

.break-text {
  margin-top: 25px;
  font-size: 1.7em;
  font-weight: 900;
  color: white;
}

.critical-alert {
  color: #ff6b9d;
  animation: blink 1s infinite;
}

@keyframes blink {
  0%, 100% { opacity: 1; }
  50% { opacity: 0.6; }
}

.progress-bar {
  height: 25px;
  background: rgba(255, 255, 255, 0.25);
  border-radius: 15px;
  overflow: hidden;
  margin-bottom: 35px;
  border: 3px solid rgba(255, 255, 255, 0.3);
}

.light-mode .progress-bar {
  background: rgba(255, 192, 203, 0.3);
  border: 3px solid rgba(255, 107, 157, 0.2);
}

.progress-fill {
  height: 100%;
  transition: width 0.3s ease;
  border-radius: 15px;
  box-shadow: 0 0 15px rgba(255, 255, 255, 0.5);
}

.timer-controls {
  display: flex;
  gap: 18px;
  justify-content: center;
}

.btn {
  padding: 18px 45px;
  font-size: 1.3em;
  font-weight: 800;
  border: none;
  border-radius: 18px;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 8px 20px rgba(255, 107, 157, 0.3);
}

.btn:hover {
  transform: translateY(-5px) scale(1.05);
  box-shadow: 0 12px 30px rgba(255, 107, 157, 0.5);
}

.btn:active {
  transform: translateY(-2px);
}

.btn-pause {
  background: linear-gradient(135deg, #ff6b9d 0%, #ff8fab 100%);
  color: white;
}

.btn-stop {
  background: linear-gradient(135deg, #ffc0cb 0%, #ffb0be 100%);
  color: #ff6b9d;
}

.paused-overlay {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background: rgba(255, 107, 157, 0.95);
  color: white;
  padding: 50px 70px;
  border-radius: 30px;
  pointer-events: none;
  z-index: 100;
  text-align: center;
  box-shadow: 0 15px 40px rgba(255, 107, 157, 0.6);
  border: 4px solid white;
}

.pause-main {
  font-size: 2.5em;
  font-weight: 900;
  margin-bottom: 25px;
}

.pause-tip {
  font-size: 1.3em;
  line-height: 1.7;
  opacity: 0.95;
  font-weight: 600;
}

.completion-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(135deg, rgba(255, 107, 157, 0.95) 0%, rgba(255, 77, 138, 0.95) 100%);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 200;
  animation: fadeIn 0.3s ease;
}

@keyframes fadeIn {
  from { opacity: 0; }
  to { opacity: 1; }
}

.completion-message {
  color: white;
  font-size: 3.5em;
  font-weight: 900;
  text-align: center;
  animation: celebration 0.6s ease;
  padding: 0 45px;
  text-shadow: 4px 4px 8px rgba(0, 0, 0, 0.3);
}

@keyframes celebration {
  0% { transform: scale(0.5) rotate(-5deg); }
  50% { transform: scale(1.1) rotate(5deg); }
  100% { transform: scale(1) rotate(0deg); }
}

/* Ï≤¥ÌÅ¨Î¶¨Ïä§Ìä∏ ÌôîÎ©¥ - ÌïòÌä∏ Ï≤¥ÌÅ¨Î∞ïÏä§ */
.checklist-header, .notes-header {
  margin-bottom: 35px;
}

.checklist-progress {
  text-align: center;
  color: white;
  font-size: 1.3em;
  margin-top: 15px;
  font-weight: 700;
}

.light-mode .checklist-progress {
  color: #ff6b9d;
}

.checklist-input-box {
  display: flex;
  gap: 12px;
  margin-bottom: 35px;
}

.checklist-input-box input {
  flex: 1;
  padding: 18px 25px;
  border: 3px solid #ffc0cb;
  border-radius: 15px;
  font-size: 16px;
  outline: none;
  transition: border-color 0.3s;
  font-weight: 600;
}

.checklist-input-box input:focus {
  border-color: #ff6b9d;
}

.checklist-input-box button {
  padding: 18px 35px;
  background: linear-gradient(135deg, #ff6b9d 0%, #ff8fab 100%);
  color: white;
  border: none;
  border-radius: 15px;
  font-size: 16px;
  font-weight: 800;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 5px 15px rgba(255, 107, 157, 0.3);
}

.checklist-input-box button:hover {
  transform: translateY(-3px);
  box-shadow: 0 8px 20px rgba(255, 107, 157, 0.5);
}

.checklist-list {
  background: white;
  border-radius: 25px;
  padding: 35px;
  box-shadow: 0 10px 30px rgba(255, 107, 157, 0.2);
  min-height: 300px;
  border: 4px solid #ffc0cb;
}

.empty-message {
  text-align: center;
  color: #ff6b9d;
  font-size: 1.3em;
  padding: 70px 20px;
  font-weight: 700;
}

.checklist-item {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 18px 0;
  border-bottom: 2px dashed #ffc0cb;
}

.checklist-item:last-child {
  border-bottom: none;
}

.checklist-content {
