/* ÌÜ†Í∏Ä Ïä§ÏúÑÏπò - ÌïòÌä∏ Î™®Ïñë */
.toggle {
  position: relative;
  display: inline-block;
  width: 65px;
  height: 36px;
}

.toggle input {
  opacity: 0;
  width: 0;
  height: 0;
}

.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ffc0cb;
  transition: .4s;
  border-radius: 36px;
}

.slider:before {
  position: absolute;
  content: "";
  height: 28px;
  width: 28px;
  left: 4px;
  bottom: 4px;
  background-color: white;
  transition: .4s;
  border-radius: 50%;
}

input:checked + .slider {
  background: linear-gradient(135deg, #ff6b9d 0%, #ff8fab 100%);
}

input:checked + .slider:before {
  transform: translateX(29px);
}

